<template>
	<view class="content">
		<uni-card :title="item.checakArea" v-for="(item,index) in policeList" :key="index">
			<uni-data-checkbox mode="list" multiple v-model="item.checkedPolice" :localdata="item.list" ></uni-data-checkbox>
		</uni-card>
		<view class="flex flex-direction" style="padding:30rpx"><button class="cu-btn bg-blue lg" @click="confirm">确 定</button></view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			areas: '',
			policeList: [],
			checkedPolice: [],
			allPoliceList:[]
		};
	},
	onLoad() {},
	onShow() {
		this.areas = uni.getStorageSync('checkedArea');
		this.getGridByAreaName();
	},
	methods: {
		getGridByAreaName() {
			let arr = []
			taskAPI.getGridByAreaName({ areaName: this.areas }).then(res => {
				if (res.code == 200) {
					// this.policeList = res.data
					this.policeList = res.data.map(item => {
						let list = [];
						let checkedPolice=[]
						item.gridPoliceList.map(i => {
							arr.push({text:i.chargePersonName,value:i.chargePersonId})
							list.push({text:i.chargePersonName,value:i.chargePersonId});
							let police = uni.getStorageSync('checkedPoliceId')
							if(police){
								if(police.split(',').indexOf(i.chargePersonId)>-1){
									checkedPolice.push(i.chargePersonId)
								}
								item.checkedPolice=checkedPolice
							}
							
						});
						item.list=list
						return item
					});
				}
			});
			this.allPoliceList=arr
			
		},
		confirm(){
			for(let i in this.policeList){
				this.checkedPolice=[...this.checkedPolice,...this.policeList[i].checkedPolice]
			}
			if (this.checkedPolice.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请选择网格警员'
				});
			} else {
				let str = this.checkedPolice.join(',');
				let strName=this.findAllPoliceName(this.checkedPolice).join(',')
				uni.setStorageSync('checkedPoliceId', str);
				uni.setStorageSync('checkedPoliceName', strName);
				uni.navigateBack()
			}
			
		},
		findAllPoliceName(idList){
			let arr=[]
			for(let i in this.allPoliceList){
				if(idList.indexOf(this.allPoliceList[i].value)>-1){
					arr.push(this.allPoliceList[i].text)
				}
			}
			return arr
		}
	}
};
</script>

<style scoped></style>
